@import "../../variables.modules";

.pink {
  color: $pink;
}

.green {
  color: $green;
}

.skills {
  display: grid;
  grid-template-columns: 1fr;
  @media only screen and (min-width: 940px) {
    grid-template-columns: 1fr 1fr;
  }

  li {
    line-height: 0.8;
  }
}

.bioText {
  white-space: pre-line;
  line-height: 1.5;
}

/* Mobile responsiveness */
@media only screen and (max-width: 600px) {
  .bioText {
    font-size: 0.9rem;       /* reduce text size */
    line-height: 1.4;
  }

  .skills li {
    font-size: 0.85rem;      /* smaller bullet size */
    line-height: 1.2;
  }

  /* Terminal wrapper if exists */
  .terminalWrapper,
  .terminal {
    width: 95% !important;   /* fill screen */
    padding: 1rem !important;
  }

  ul {
    padding-left: 1rem;
  }
}